<script>
	import DialMIDI from './components/DialMIDI.svelte'
	let controls = []

	function addControl() {
		const hue = Math.random() * 360 + 'deg'
		const sat = Math.random() * 20 + 50 + '%'
		const lit = '65%'

		controls = [...controls, { color: `hsl(${hue}, ${sat}, ${lit})` }]
	}
</script>

<main>
	<section class='grid'>
		{#each controls as control, i (control.color+i)}
			<DialMIDI id={ i } color={ control.color } on:input={ ({ detail }) => {
				control.value = detail
				controls = [...controls]	
			}} />
		{/each}
		<button class='add-midi' on:click={ addControl }>+ Add MIDI Input</button>
	</section>
	<section>
		<svg viewBox='0 0 20 10' style='max-width: 720px; border: solid 4px;'>
			<rect x={ (controls[0] && controls[0].value !== undefined) ? controls[0].value : 5 }
				  y={ (controls[1] && controls[1].value !== undefined) ? controls[1].value : 2.5 }
				  width={ (controls[2] && controls[2].value !== undefined) ? controls[2].value : 10 }
				  height={ (controls[3] && controls[3].value !== undefined) ? controls[3].value : 5 }
				  fill={`hsl(${ (controls[4] && controls[4].value !== undefined) ? controls[4].value : 190 }deg, 60%, 60%)`}  />
		</svg>
	</section>
</main>

<style>
	main {
		text-align: center;
		padding: 2vh 1vw;
		margin: 0 auto;
		max-width: 100%;
	}
	
	section.grid {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		margin-block-end: 1em;
	}

	button.add-midi {
		font-size: 1.8rem;
		align-self: center;
		width: fit-content;
		justify-self: center;
		border: none;
		background: #343434;
		color: white;
		padding: .25em 1em;
		border-radius: .15em;
	}

	@media (min-width: 640px) {
		main {
			max-width: none;
		}
	}
</style>